import { awsCloudDashboardDefaultFilters, awsCloudDashboardStateKey, awsCloudDashboardTabFilters, getQueryForWidget, getQueryForWidgetTabs, } from './awsCloudDashboardCommon';
export const selectAwsCloudDashboardState = (state) => state[awsCloudDashboardStateKey];
export const selectWidgets = (state) => selectAwsCloudDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAwsCloudDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign(Object.assign({}, awsCloudDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, awsCloudDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, filter), { time_scope_value: -2 })),
        current: getQueryForWidget(filter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
