import { awsCostOverviewStateKey } from './awsCostOverviewCommon';
export const selectAwsCostOverviewState = (state) => state[awsCostOverviewStateKey];
export const selectWidgets = (state) => selectAwsCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAwsCostOverviewState(state).currentWidgets;
