import { gcpHistoricalDataStateKey } from './gcpHistoricalDataCommon';
export const selectGcpHistoricalDataState = (state) => state[gcpHistoricalDataStateKey];
export const selectWidgets = (state) => selectGcpHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectGcpHistoricalDataState(state).currentWidgets;
