import { getType } from 'typesafe-actions';
import { fetchForecastFailure, fetchForecastRequest, fetchForecastSuccess } from './forecastActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function forecastReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchForecastRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.forecastId, 1 /* inProgress */) });
        case getType(fetchForecastSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.forecastId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.forecastId, Object.assign(Object.assign({}, action.payload), { timeRequested: Date.now() })), errors: new Map(state.errors).set(action.meta.forecastId, null) });
        case getType(fetchForecastFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.forecastId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.forecastId, action.payload) });
        default:
            return state;
    }
}
