import { ocpCostOverviewStateKey } from './ocpCostOverviewCommon';
export const selectOcpCostOverviewState = (state) => state[ocpCostOverviewStateKey];
export const selectWidgets = (state) => selectOcpCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpCostOverviewState(state).currentWidgets;
