import { addNotification } from '@redhat-cloud-services/frontend-components-notifications';
import { deleteCostModel as apiDeleteCostModel, fetchCostModels as apiGetCostModels, updateCostModel as apiUpdateCostModel, } from 'api/costModels';
import i18next from 'i18next';
import { createAction, createAsyncAction } from 'typesafe-actions';
export const updateFilterToolbar = createAction('fetch/costModels/filter')();
export const selectCostModel = createAction('select/costModels')();
export const resetCostModel = createAction('reset/costModels')();
export const setCostModelDialog = createAction('display/costModels/dialog')();
export const { request: fetchCostModelsRequest, success: fetchCostModelsSuccess, failure: fetchCostModelsFailure, } = createAsyncAction('fetch/costModels/request', 'fetch/costModels/success', 'fetch/costModels/failure')();
export const fetchCostModels = (query = '') => {
    return (dispatch) => {
        dispatch(fetchCostModelsRequest());
        return apiGetCostModels(query)
            .then(res => {
            dispatch(fetchCostModelsSuccess(res));
        })
            .catch(err => {
            dispatch(fetchCostModelsFailure(err));
        });
    };
};
export const { request: updateCostModelsRequest, success: updateCostModelsSuccess, failure: updateCostModelsFailure, } = createAsyncAction('update/costModels/request', 'update/costModels/success', 'update/costModels/failure')();
export const updateCostModel = (uuid, request, dialog = null) => {
    return (dispatch) => {
        dispatch(updateCostModelsRequest());
        return apiUpdateCostModel(uuid, request)
            .then(res => {
            dispatch(updateCostModelsSuccess(res));
            if (dialog !== null) {
                fetchCostModels(`uuid=${uuid}`)(dispatch);
                dispatch(setCostModelDialog({ name: dialog, isOpen: false }));
            }
        })
            .catch(err => {
            dispatch(updateCostModelsFailure(err));
        });
    };
};
export const { request: deleteCostModelsRequest, success: deleteCostModelsSuccess, failure: deleteCostModelsFailure, } = createAsyncAction('delete/costModels/request', 'delete/costModels/success', 'delete/costModels/failure')();
export const deleteCostModel = (uuid, dialog = '', history = null) => {
    return (dispatch) => {
        dispatch(deleteCostModelsRequest());
        return apiDeleteCostModel(uuid)
            .then(() => {
            dispatch(deleteCostModelsSuccess());
            dispatch(resetCostModel());
            fetchCostModels()(dispatch);
            if (dialog !== null) {
                if (dialog === 'deleteCostModel' && history) {
                    history.push('/cost-models');
                }
                dispatch(setCostModelDialog({ name: dialog, isOpen: false }));
            }
        })
            .catch(err => {
            dispatch(deleteCostModelsFailure(err));
        });
    };
};
export const redirectToCostModelFromSourceUuid = (source_uuid, history) => {
    return (dispatch) => {
        return apiGetCostModels(`source_uuid=${source_uuid}`)
            .then(res => {
            const uuid = res.data.data[0].uuid;
            insights.chrome.appNavClick({ id: 'cost-models', secondaryNav: null });
            history.push(`/cost-models/${uuid}`);
        })
            .catch(() => {
            dispatch(addNotification({
                title: i18next.t('cost_models_router.error_title'),
                description: i18next.t('cost_models_router.server_error'),
                variant: 'danger',
                dismissable: true,
            }));
        });
    };
};
