import { getType } from 'typesafe-actions';
import { closeProvidersModal, openProvidersModal, toggleSidebar } from './uiActions';
export const defaultState = {
    isExportModalOpen: false,
    isProvidersModalOpen: false,
    isSidebarOpen: false,
};
export const stateKey = 'ui';
export function uiReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(closeProvidersModal):
            return Object.assign(Object.assign({}, state), { isProvidersModalOpen: false });
        case getType(openProvidersModal):
            return Object.assign(Object.assign({}, state), { isProvidersModalOpen: true });
        case getType(toggleSidebar):
            return Object.assign(Object.assign({}, state), { isSidebarOpen: !state.isSidebarOpen });
        default:
            return state;
    }
}
