import { getQueryForWidget, getQueryForWidgetTabs, ocpDashboardDefaultFilters, ocpDashboardStateKey, ocpDashboardTabFilters, } from './ocpDashboardCommon';
export const selectOcpDashboardState = (state) => state[ocpDashboardStateKey];
export const selectWidgets = (state) => selectOcpDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign(Object.assign({}, ocpDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, ocpDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, defaultFilter), { time_scope_value: -2 })),
        current: getQueryForWidget(defaultFilter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
