import { ocpHistoricalDataStateKey } from './ocpHistoricalDataCommon';
export const selectOcpHistoricalDataState = (state) => state[ocpHistoricalDataStateKey];
export const selectWidgets = (state) => selectOcpHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpHistoricalDataState(state).currentWidgets;
