import { azureDashboardDefaultFilters, azureDashboardStateKey, azureDashboardTabFilters, getQueryForWidget, getQueryForWidgetTabs, } from './azureDashboardCommon';
export const selectAzureDashboardState = (state) => state[azureDashboardStateKey];
export const selectWidgets = (state) => selectAzureDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAzureDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign(Object.assign({}, azureDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, azureDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, filter), { time_scope_value: -2 })),
        current: getQueryForWidget(filter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
