import { getQuery } from 'api/queries/ocpCloudQuery';
export const ocpUsageDashboardStateKey = 'ocpUsageDashboard';
export const ocpUsageDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const ocpUsageDashboardTabFilters = Object.assign(Object.assign({}, ocpUsageDashboardDefaultFilters), { limit: 3 });
// Todo: cluster, project, node
export function getGroupByForTab(tab) {
    switch (tab) {
        case "projects" /* projects */:
            return { project: '*' };
        case "clusters" /* clusters */:
            return { cluster: '*' };
        case "nodes" /* nodes */:
            return { node: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = ocpUsageDashboardDefaultFilters) {
    const query = {
        filter,
    };
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = ocpUsageDashboardDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
