import { fetchProviders as apiGetSources } from 'api/providers';
import { createAction, createAsyncAction } from 'typesafe-actions';
export const updateFilterToolbar = createAction('fetch/source/filter')();
export const { request: fetchSourcesRequest, success: fetchSourcesSuccess, failure: fetchSourcesFailure, } = createAsyncAction('fetch/source/request', 'fetch/source/success', 'fetch/source/failure')();
export const fetchSources = (query = '') => {
    return (dispatch) => {
        dispatch(fetchSourcesRequest());
        return apiGetSources(query)
            .then(res => {
            dispatch(fetchSourcesSuccess(res));
        })
            .catch(err => {
            dispatch(fetchSourcesFailure(err));
        });
    };
};
