import { azureCostOverviewStateKey } from './azureCostOverviewCommon';
export const selectAzureCostOverviewState = (state) => state[azureCostOverviewStateKey];
export const selectWidgets = (state) => selectAzureCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAzureCostOverviewState(state).currentWidgets;
