import { selectPagination } from 'store/djangoUtils/pagination';
import { selectQuery } from 'store/djangoUtils/query';
import { stateKey } from './reducer';
export const sourcesState = (state) => state[stateKey];
export const sources = (state) => {
    const srcs = sourcesState(state).sources;
    if (srcs) {
        return srcs.data;
    }
    return [];
};
export const status = (state) => sourcesState(state).status;
export const error = (state) => sourcesState(state).error;
export const currentFilterValue = (state) => sourcesState(state).currentFilterValue;
export const currentFilterType = (state) => sourcesState(state).currentFilterType;
export const query = selectQuery((state) => sourcesState(state).sources, [
    'name',
    'type',
    'offset',
    'limit',
]);
export const pagination = selectPagination((state) => sourcesState(state).sources);
