import { getType } from 'typesafe-actions';
import { fetchOrgFailure, fetchOrgRequest, fetchOrgSuccess } from './orgActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function orgReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchOrgRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.orgId, 1 /* inProgress */) });
        case getType(fetchOrgSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.orgId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.orgId, Object.assign(Object.assign({}, action.payload), { timeRequested: Date.now() })), errors: new Map(state.errors).set(action.meta.orgId, null) });
        case getType(fetchOrgFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.orgId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.orgId, action.payload) });
        default:
            return state;
    }
}
