import { fetchRateMetrics } from 'api/metrics';
import { createAsyncAction } from 'typesafe-actions';
export const { request: fetchMetricsRequest, success: fetchMetricsSuccess, failure: fetchMetricsFailure, } = createAsyncAction('fetch/metrics/request', 'fetch/metrics/success', 'fetch/metrics/failure')();
export const fetchMetrics = (source_type = '') => {
    return (dispatch) => {
        dispatch(fetchMetricsRequest());
        return fetchRateMetrics(source_type)
            .then(res => {
            dispatch(fetchMetricsSuccess(res));
        })
            .catch(err => {
            dispatch(fetchMetricsFailure(err));
        });
    };
};
