import { getQueryForWidget, getQueryForWidgetTabs, ocpSupplementaryDashboardDefaultFilters, ocpSupplementaryDashboardStateKey, ocpSupplementaryDashboardTabFilters, } from './ocpSupplementaryDashboardCommon';
export const selectOcpSupplementaryDashboardState = (state) => state[ocpSupplementaryDashboardStateKey];
export const selectWidgets = (state) => selectOcpSupplementaryDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpSupplementaryDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign(Object.assign({}, ocpSupplementaryDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, ocpSupplementaryDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, defaultFilter), { time_scope_value: -2 })),
        current: getQueryForWidget(defaultFilter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
