import { getType } from 'typesafe-actions';
import { fetchSourcesFailure, fetchSourcesRequest, fetchSourcesSuccess, updateFilterToolbar } from './actions';
export const stateKey = 'sources';
export const defaultState = {
    sources: null,
    error: null,
    status: 0 /* none */,
    currentFilterType: 'name',
    currentFilterValue: '',
};
export const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case getType(fetchSourcesRequest):
            return Object.assign(Object.assign({}, state), { status: 1 /* inProgress */ });
        case getType(fetchSourcesSuccess):
            return Object.assign(Object.assign({}, state), { currentFilterValue: '', status: 2 /* complete */, error: null, sources: action.payload.data });
        case getType(fetchSourcesFailure):
            return Object.assign(Object.assign({}, state), { status: 2 /* complete */, error: action.payload });
        case getType(updateFilterToolbar):
            return Object.assign(Object.assign({}, state), action.payload);
        default:
            return state;
    }
};
