import { getType } from 'typesafe-actions';
import { fetchPriceListFailure, fetchPriceListRequest, fetchPriceListSuccess } from './actions';
export const stateKey = 'priceList';
export const defaultState = {
    rates: new Map(),
    error: new Map(),
    status: new Map(),
};
export function reducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchPriceListRequest):
            return Object.assign(Object.assign({}, state), { status: new Map(state.status).set(action.payload.providerUuid, 1 /* inProgress */) });
        case getType(fetchPriceListSuccess):
            return Object.assign(Object.assign({}, state), { status: new Map(state.status).set(action.meta.providerUuid, 2 /* complete */), rates: new Map(state.rates).set(action.meta.providerUuid, Object.assign(Object.assign({}, action.payload), { timeRequested: Date.now() })), error: new Map(state.error).set(action.meta.providerUuid, null) });
        case getType(fetchPriceListFailure):
            return Object.assign(Object.assign({}, state), { error: new Map(state.error).set(action.meta.providerUuid, action.payload), status: new Map(state.status).set(action.meta.providerUuid, 2 /* complete */) });
        default:
            return state;
    }
}
