import { getType } from 'typesafe-actions';
import { fetchExportFailure, fetchExportRequest, fetchExportSuccess } from './exportActions';
const defaultState = {
    byId: new Map(),
    fetchStatus: new Map(),
    errors: new Map(),
};
export function exportReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchExportRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.reportId, 1 /* inProgress */) });
        case getType(fetchExportSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.reportId, {
                    data: action.payload,
                    timeRequested: Date.now(),
                }), errors: new Map(state.errors).set(action.meta.reportId, null) });
        case getType(fetchExportFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.reportId, action.payload) });
        default:
            return state;
    }
}
