import { azureHistoricalDataStateKey } from './azureHistoricalDataCommon';
export const selectAzureHistoricalDataState = (state) => state[azureHistoricalDataStateKey];
export const selectWidgets = (state) => selectAzureHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAzureHistoricalDataState(state).currentWidgets;
