import { parseApiError } from 'pages/costModels/createCostModelWizard/parseError';
import { stateKey } from './reducer';
export const metricsState = (state) => state[stateKey];
export const status = (state) => metricsState(state).status;
export const error = (state) => {
    const err = metricsState(state).error;
    if (err === null) {
        return '';
    }
    return parseApiError(err);
};
export const metrics = (state) => {
    const metricsPayload = metricsState(state).metrics;
    if (metricsPayload === null) {
        return {};
    }
    return metricsPayload.data.reduce((acc, curr) => {
        const prev = acc[curr.label_metric] ? Object.assign({}, acc[curr.label_metric]) : {};
        return Object.assign(Object.assign({}, acc), { [curr.label_metric]: Object.assign(Object.assign({}, prev), { [curr.label_measurement]: curr }) });
    }, {});
};
export const maxRate = (state) => {
    const metricsPayload = metricsState(state).metrics;
    if (metricsPayload === null) {
        return 0;
    }
    return metricsPayload.data.length;
};
export const costTypes = (state) => {
    const metricsPayload = metricsState(state).metrics;
    if (metricsPayload === null) {
        return [];
    }
    return metricsPayload.data.reduce((acc, curr) => {
        if (acc.includes(curr.default_cost_type)) {
            return acc;
        }
        return [curr.default_cost_type, ...acc];
    }, []);
};
