import { getQuery } from 'api/queries/ocpQuery';
export const ocpDashboardStateKey = 'ocpDashboard';
export const ocpDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const ocpDashboardTabFilters = Object.assign(Object.assign({}, ocpDashboardDefaultFilters), { limit: 3 });
// Todo: cluster, project, node
export function getGroupByForTab(tab) {
    switch (tab) {
        case "projects" /* projects */:
            return { project: '*' };
        case "clusters" /* clusters */:
            return { cluster: '*' };
        case "nodes" /* nodes */:
            return { node: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = ocpDashboardDefaultFilters, props) {
    const query = Object.assign({ filter }, (props ? props : {}));
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = ocpDashboardDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
