import { awsHistoricalDataStateKey } from './awsHistoricalDataCommon';
export const selectAwsHistoricalDataState = (state) => state[awsHistoricalDataStateKey];
export const selectWidgets = (state) => selectAwsHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAwsHistoricalDataState(state).currentWidgets;
