import { getType } from 'typesafe-actions';
import { deleteCostModelsFailure, deleteCostModelsRequest, deleteCostModelsSuccess, fetchCostModelsFailure, fetchCostModelsRequest, fetchCostModelsSuccess, resetCostModel, selectCostModel, setCostModelDialog, updateCostModelsFailure, updateCostModelsRequest, updateCostModelsSuccess, updateFilterToolbar, } from './actions';
export const stateKey = 'costModels';
export const defaultState = {
    costModels: null,
    error: null,
    status: 0 /* none */,
    currentFilterType: 'name',
    currentFilterValue: '',
    isDialogOpen: {
        deleteRate: false,
        deleteSource: false,
        addSource: false,
        addRate: false,
        updateRate: false,
        deleteCostModel: false,
        updateCostModel: false,
        deleteMarkup: false,
        updateMarkup: false,
        createWizard: false,
    },
    dialogData: null,
    update: {
        error: null,
        status: 0 /* none */,
        current: null,
    },
    delete: {
        error: null,
        status: 0 /* none */,
    },
};
export const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case getType(resetCostModel):
            return Object.assign(Object.assign({}, state), { update: Object.assign(Object.assign({}, state.update), { current: null }) });
        case getType(selectCostModel):
            return Object.assign(Object.assign({}, state), { update: Object.assign(Object.assign({}, state.update), { current: action.payload }) });
        case getType(updateCostModelsRequest):
            return Object.assign(Object.assign({}, state), { update: Object.assign(Object.assign({}, state.update), { status: 1 /* inProgress */ }) });
        case getType(updateCostModelsSuccess):
            return Object.assign(Object.assign({}, state), { update: {
                    error: null,
                    status: 2 /* complete */,
                    current: action.payload.data,
                } });
        case getType(updateCostModelsFailure):
            return Object.assign(Object.assign({}, state), { update: Object.assign(Object.assign({}, state.update), { status: 2 /* complete */, error: action.payload }) });
        case getType(fetchCostModelsRequest):
            return Object.assign(Object.assign({}, state), { status: 1 /* inProgress */ });
        case getType(fetchCostModelsSuccess):
            return Object.assign(Object.assign({}, state), { currentFilterValue: '', status: 2 /* complete */, error: null, costModels: action.payload.data });
        case getType(fetchCostModelsFailure):
            return Object.assign(Object.assign({}, state), { status: 2 /* complete */, error: action.payload });
        case getType(deleteCostModelsRequest):
            return Object.assign(Object.assign({}, state), { delete: Object.assign(Object.assign({}, state.delete), { status: 1 /* inProgress */ }) });
        case getType(deleteCostModelsSuccess):
            return Object.assign(Object.assign({}, state), { delete: {
                    error: null,
                    status: 2 /* complete */,
                } });
        case getType(deleteCostModelsFailure):
            return Object.assign(Object.assign({}, state), { delete: {
                    status: 2 /* complete */,
                    error: action.payload,
                } });
        case getType(updateFilterToolbar):
            return Object.assign(Object.assign({}, state), action.payload);
        case getType(setCostModelDialog):
            return Object.assign(Object.assign({}, state), { isDialogOpen: Object.assign(Object.assign({}, state.isDialogOpen), { [action.payload.name]: action.payload.isOpen }), dialogData: action.payload.meta });
        default:
            return state;
    }
};
