import { getQuery } from 'api/queries/awsQuery';
export const awsCloudDashboardStateKey = 'awsCloudDashboard';
export const awsCloudDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const awsCloudDashboardTabFilters = Object.assign(Object.assign({}, awsCloudDashboardDefaultFilters), { limit: 3 });
export function getGroupByForTab(widget) {
    switch (widget.currentTab) {
        case "services" /* services */:
            // Use group_by for service tab and filter for others -- https://github.com/project-koku/koku-ui/issues/846
            return {
                service: widget.tabsFilter && widget.tabsFilter.service ? widget.tabsFilter.service : '*',
            };
        case "accounts" /* accounts */:
            return { account: '*' };
        case "regions" /* regions */:
            return { region: '*' };
        case "instance_type" /* instanceType */:
            return { instance_type: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = awsCloudDashboardDefaultFilters, props) {
    const query = Object.assign({ filter }, (props ? props : {}));
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = awsCloudDashboardDefaultFilters) {
    const group_by = getGroupByForTab(widget);
    const newFilter = Object.assign({}, JSON.parse(JSON.stringify(filter)));
    // Use group_by for service tab and filter for others -- https://github.com/project-koku/koku-ui/issues/846
    if (widget.currentTab === "services" /* services */ && widget.tabsFilter && widget.tabsFilter.service) {
        newFilter.service = undefined;
    }
    const query = {
        filter: newFilter,
        group_by,
    };
    return getQuery(query);
}
