import axios from 'axios';
export const ForecastTypePaths = {
    ["cost" /* cost */]: 'forecasts/azure/costs/',
};
export function runForecast(forecastType, query) {
    const insights = window.insights;
    const path = ForecastTypePaths[forecastType];
    if (insights && insights.chrome && insights.chrome.auth && insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`${path}?${query}`);
        });
    }
    else {
        return axios.get(`${path}?${query}`);
    }
}
