import { getQuery } from 'api/queries/azureQuery';
export const azureDashboardStateKey = 'azureDashboard';
export const azureDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const azureDashboardTabFilters = Object.assign(Object.assign({}, azureDashboardDefaultFilters), { limit: 3 });
export function getGroupByForTab(widget) {
    switch (widget.currentTab) {
        case "service_names" /* service_names */:
            // Use group_by for service tab and filter for others -- https://github.com/project-koku/koku-ui/issues/846
            return {
                service_name: widget.tabsFilter && widget.tabsFilter.service_name ? widget.tabsFilter.service_name : '*',
            };
        case "subscription_guids" /* subscription_guids */:
            return { subscription_guid: '*' };
        case "resource_locations" /* resource_locations */:
            return { resource_location: '*' };
        case "instance_type" /* instanceType */:
            return { instance_type: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = azureDashboardDefaultFilters, props) {
    const query = Object.assign({ filter }, (props ? props : {}));
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = azureDashboardDefaultFilters) {
    const group_by = getGroupByForTab(widget);
    const newFilter = Object.assign({}, JSON.parse(JSON.stringify(filter)));
    // Use group_by for service tab and filter for others -- https://github.com/project-koku/koku-ui/issues/846
    if (widget.currentTab === "service_names" /* service_names */ && widget.tabsFilter && widget.tabsFilter.service_name) {
        newFilter.service = undefined;
    }
    const query = {
        filter: newFilter,
        group_by,
    };
    return getQuery(query);
}
