import axios from 'axios';
export function fetchCostModels(query = '') {
    return axios.get(`cost-models/${query && '?'}${query}`);
}
export function fetchCostModel(uuid) {
    return axios.get(`cost-models/${uuid}/`);
}
export function addCostModel(request) {
    return axios.post('cost-models/', request);
}
export function updateCostModel(uuid, request) {
    return axios.put(`cost-models/${uuid}/`, request);
}
export function deleteCostModel(uuid) {
    return axios.delete(`cost-models/${uuid}/`);
}
