import { azureCloudDashboardDefaultFilters, azureCloudDashboardStateKey, azureCloudDashboardTabFilters, getQueryForWidget, getQueryForWidgetTabs, } from './azureCloudDashboardCommon';
export const selectAzureCloudDashboardState = (state) => state[azureCloudDashboardStateKey];
export const selectWidgets = (state) => selectAzureCloudDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectAzureCloudDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign(Object.assign({}, azureCloudDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, azureCloudDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, filter), { time_scope_value: -2 })),
        current: getQueryForWidget(filter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
