import { getQueryForWidget, getQueryForWidgetTabs, ocpUsageDashboardDefaultFilters, ocpUsageDashboardStateKey, ocpUsageDashboardTabFilters, } from './ocpUsageDashboardCommon';
export const selectOcpUsageDashboardState = (state) => state[ocpUsageDashboardStateKey];
export const selectWidgets = (state) => selectOcpUsageDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpUsageDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const defaultFilter = Object.assign(Object.assign({}, ocpUsageDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, ocpUsageDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, defaultFilter), { time_scope_value: -2 })),
        current: getQueryForWidget(defaultFilter),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
