import { runExport } from 'api/exports/exportUtils';
import { getExportId } from 'store/exports/exportCommon';
import { selectExport, selectExportFetchStatus } from 'store/exports/exportSelectors';
import { createAction } from 'typesafe-actions';
const expirationMS = 30 * 60 * 1000; // 30 minutes
export const fetchExportRequest = createAction('report/request')();
export const fetchExportSuccess = createAction('report/success')();
export const fetchExportFailure = createAction('report/failure')();
export function exportReport(reportPathsType, reportType, query) {
    return (dispatch, getState) => {
        if (!isExportExpired(getState(), reportPathsType, reportType, query)) {
            return;
        }
        const meta = {
            reportId: getExportId(reportPathsType, reportType, query),
        };
        dispatch(fetchExportRequest(meta));
        runExport(reportPathsType, reportType, query)
            .then(res => {
            dispatch(fetchExportSuccess(res.data, meta));
        })
            .catch(err => {
            dispatch(fetchExportFailure(err, meta));
        });
    };
}
function isExportExpired(state, reportPathsType, reportType, query) {
    const report = selectExport(state, reportPathsType, reportType, query);
    const fetchStatus = selectExportFetchStatus(state, reportPathsType, reportType, query);
    if (fetchStatus === 1 /* inProgress */) {
        return false;
    }
    if (!report) {
        return true;
    }
    const now = Date.now();
    return now > report.timeRequested + expirationMS;
}
