import { getType } from 'typesafe-actions';
import { fetchRbacFailure, fetchRbacRequest, fetchRbacSuccess } from './actions';
export const stateKey = 'RBAC';
export const defaultState = {
    isOrgAdmin: false,
    permissions: null,
    status: 0 /* none */,
    error: null,
};
export const reducer = (state = defaultState, action) => {
    switch (action.type) {
        case getType(fetchRbacRequest):
            return Object.assign(Object.assign({}, state), { status: 1 /* inProgress */ });
        case getType(fetchRbacSuccess):
            return Object.assign(Object.assign({}, action.payload), { status: 2 /* complete */, error: null });
        case getType(fetchRbacFailure):
            return Object.assign(Object.assign({}, state), { status: 2 /* complete */, error: action.payload });
        default:
            return state;
    }
};
