import { getType } from 'typesafe-actions';
import { setWidgetTab } from './azureDashboardActions';
import { costSummaryWidget, databaseWidget, networkWidget, storageWidget, virtualMachineWidget, } from './azureDashboardWidgets';
export const defaultState = {
    currentWidgets: [
        costSummaryWidget.id,
        virtualMachineWidget.id,
        storageWidget.id,
        networkWidget.id,
        databaseWidget.id,
    ],
    widgets: {
        [costSummaryWidget.id]: costSummaryWidget,
        [virtualMachineWidget.id]: virtualMachineWidget,
        [databaseWidget.id]: databaseWidget,
        [networkWidget.id]: networkWidget,
        [storageWidget.id]: storageWidget,
    },
};
export function azureDashboardReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setWidgetTab):
            return Object.assign(Object.assign({}, state), { widgets: Object.assign(Object.assign({}, state.widgets), { [action.payload.id]: Object.assign(Object.assign({}, state.widgets[action.payload.id]), { currentTab: action.payload.tab }) }) });
        default:
            return state;
    }
}
