import { gcpDashboardDefaultFilters, gcpDashboardStateKey, gcpDashboardTabFilters, getQueryForWidget, getQueryForWidgetTabs, } from './gcpDashboardCommon';
export const selectGcpDashboardState = (state) => state[gcpDashboardStateKey];
export const selectWidgets = (state) => selectGcpDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectGcpDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign(Object.assign({}, gcpDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, gcpDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, filter), { time_scope_value: -2 })),
        current: getQueryForWidget(filter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
