import { gcpCostOverviewStateKey } from './gcpCostOverviewCommon';
export const selectGcpCostOverviewState = (state) => state[gcpCostOverviewStateKey];
export const selectWidgets = (state) => selectGcpCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectGcpCostOverviewState(state).currentWidgets;
